import { Container } from 'react-bootstrap';
import dev from '../assets/dev.png'
import dev2 from '../assets/dev2.png'
import dev3 from '../assets/dev3.png'
import style from '../css.module.css'

const DEVV = () => {
    return ( <>
     <Container fluid className={style.dele} style={{width:'70rem',marginBottom:'5rem',position:'relative',left:'5rem'}}>
        <img   style={{width:"19rem"}} src={dev}/>
        <img  style={{width:"23rem"}} src={dev2}/>
        <img  style={{width:"19rem"}} src={dev3}/>
     </Container>
    
    
    
    </>  );
}
 
export default DEVV;