import { Container } from "react-bootstrap";
import styles from "../singlecss.module.css";
import Breadcrumb from "react-bootstrap/Breadcrumb";
import one from "../assets/product_img1.jpg";
import two from "../assets/product_img2.jpg";
import three from "../assets/product_img3.jpg";
import { useState } from "react";
import CloseIcon from "@mui/icons-material/Close";
import StorefrontIcon from "@mui/icons-material/Storefront";
import { Select, FormControl, MenuItem, InputLabel } from "@mui/material";
import Productt from "../components/product";
import { useSelector } from "react-redux";
import { useDispatch } from 'react-redux';
import { addtocart,removefromcart } from '../redux/cartslice';
import { cartslice } from "../redux/cartslice";

const FullCart = () => {
  const dispatch = useDispatch();
  const productdata= useSelector((state) => state?.cart?.items);
  const [count, setCount] = useState(0);
  const handleIncrement = () => {
    setCount(count + 1);
  };

  const handleDecrement = () => {
    setCount(count - 1);
  };
  return (
    <div>
      <div className={styles.breadcrumbs}>
        <p>Shopping Cart</p>
        <Breadcrumb id={styles.bread}>
          <Breadcrumb.Item active>Home</Breadcrumb.Item>
          <Breadcrumb.Item active>Pages</Breadcrumb.Item>
          <Breadcrumb.Item active>Shopping Cart</Breadcrumb.Item>
        </Breadcrumb>
      </div>
      <br />
      <div
        style={{
          display: "flex",
          justifyContent: "space-around",
          marginBottom: "2rem",
        }}
      >
        <hr />
        
        <table class="table" style={{margin:'4rem'}}>
          <thead style={{width:'10rem'}}>
          <tr>
              <th>product</th>
              <th>Price </th>
              <th>Quantity</th>
              <th>Total</th>
              <th>Remove </th>
            </tr>
          </thead>
          <tbody style={{width:'10rem'}}>
            
           {
            productdata?.map((r)=>
            <tr>
                 <th style={{display:"flex",flexDirection:'column'}}><img style={{ width: "10rem", height: "10rem" }} src={r.image} /><p style={{fontSize:"1rem",width:"20rem"}}>{r.title}</p></th>
                 <th >{r.price}</th>
                 <th>{count}</th>
                 <th>${r.price}</th>
                 <th  onClick={() => dispatch(cartslice.actions.removefromcart(r))}><CloseIcon/></th>
            </tr>
            )
           }
          </tbody>
        </table>
      </div>
      <div
      className={styles.copoun}
        style={{
          display: "flex",
          justifyContent: "space-evenly",
          width: "100rem",
          alignContent: "center",
          flexWrap: "wrap",
        }}
      >
        <div style={{ marginBottom: "5rem" }}>
          <input
            type="text"
            placeholder="Enter Coupon Code.."
            style={{ height: "3rem" }}
          />

          <button
            style={{
              backgroundColor: "#fe324d",
              color: "white",
              border: "none",
              width: "10rem",
              height: "3rem",
              transition: "background-color 0.3s ease",
              outline: "none",
            }}
            className="boton"
            onMouseOver={(e) => {
              e.currentTarget.style.backgroundColor = "transparent";
              e.currentTarget.style.outline = "#fe324d solid 2px";
              e.currentTarget.style.color = "#fe324d ";
            }}
            onMouseOut={(e) => {
              e.currentTarget.style.backgroundColor = "#fe324d";
              e.currentTarget.style.outline = "none";
              e.currentTarget.style.color = "#ffff ";
            }}
            // onClick={() => addToCart(id, price ,amount ,product)}
            // onFocus={(e) => e.currentTarget.style.outline = 'none'}
          >
            Apply Coupon
          </button>
        </div>
        <button
          style={{
            backgroundColor: "transparent",
            color: "#16181a",
            border: "1rem #16181a",
            width: "7rem",
            height: "3rem",
            transition: "background-color 0.3s  ease",
            outline: "#16181a solid 2px",
          }}
          className="boton"
          onMouseOver={(e) => {
            e.currentTarget.style.backgroundColor = "#16181a";
            e.currentTarget.style.outline = "#16181a solid 2px";
            e.currentTarget.style.color = "#ffff ";
          }}
          onMouseOut={(e) => {
            e.currentTarget.style.backgroundColor = "transparent";
            e.currentTarget.style.outline = "#16181a solid 2px";
            e.currentTarget.style.color = "#16181a";
          }}
          // onClick={() => addToCart(id, price ,amount ,product)}
          // onFocus={(e) => e.currentTarget.style.outline = 'none'}
        >
          Update Cart
        </button>
      </div>
      {/* <hr style={{width:'50rem',display:'flex',justifyContent:'center'}}/> */}
      <div
        style={{
          display: "flex",
          justifyContent: "space-evenly",
          gap: "-20rem",
          marginBottom: "5rem",
          flexWrap: "wrap",
        }}
      >
        <div style={{ marginBottom: "5rem", width: "25rem" }}>
          <h5 style={{ fontWeight: "500" }}>Calculate Shipping</h5>

          <FormControl fullWidth>
            <InputLabel id="demo-simple-select-label">
              Choose a option...
            </InputLabel>
            <Select labelId="demo-simple-select-label" id="demo-simple-select">
            
              <MenuItem value={10}>cairo</MenuItem>
              <MenuItem value={20}>giza</MenuItem>
              <MenuItem value={30}>sharkia</MenuItem>
              <MenuItem value={10}>cairo</MenuItem>
              <MenuItem value={20}>giza</MenuItem>
              <MenuItem value={30}>sharkia</MenuItem>
              <MenuItem value={10}>cairo</MenuItem>
              <MenuItem value={20}>giza</MenuItem>
              <MenuItem value={30}>sharkia</MenuItem>
              <MenuItem value={10}>cairo</MenuItem>
              <MenuItem value={20}>giza</MenuItem>
          
            </Select>
          </FormControl>
          <div
            style={{
              gap: "1rem ",
              display: "flex",
              marginTop: "0.5rem",
              marginBottom: "0.5rem",
              flexWrap: "wrap",
            }}
          >
            <input
              style={{ width: "25rem", height: "3rem" }}
              placeholder="State / Country"
            />
            <input
              style={{ width: "25rem", height: "3rem" }}
              placeholder="PostCode / ZIP "
            />
          </div>
          <button
            style={{
              backgroundColor: "#16181a",
              color: "#fff",
              border: "1rem #16181a",
              width: "7rem",
              height: "3rem",
              transition: "background-color 0.3s  ease",
              outline: "#16181a solid 2px",
            }}
            className="boton"
            onMouseOver={(e) => {
              e.currentTarget.style.backgroundColor = "transparent";
              e.currentTarget.style.outline = "#16181a solid 2px";
              e.currentTarget.style.color = "#16181a ";
            }}
            onMouseOut={(e) => {
              e.currentTarget.style.backgroundColor = "#16181a";
              e.currentTarget.style.outline = "#16181a solid 2px";
              e.currentTarget.style.color = "#fff";
            }}
            // onClick={() => addToCart(id, price ,amount ,product)}
            // onFocus={(e) => e.currentTarget.style.outline = 'none'}
          >
            Update Totals
          </button>
        </div>
        <div
          style={{
            border: "0.01rem gray solid",
            width: "25rem",
            height: "19rem",
            padding: "2rem",
          }}
        >
          <h5>Cart Totals</h5>
          <hr />
          <div
            style={{
              display: "flex",
              justifyContent: "space-between",
              height: "1rem",
            }}
          >
            <p>Cart subtotal</p>
            <p>$349.00</p>
          </div>
          <hr />
          <div
            style={{
              display: "flex",
              justifyContent: "space-between",
              height: "1rem",
            }}
          >
            <p>Shipping</p>
            <p>Free Shipping</p>
          </div>
          <hr />
          <div
            style={{
              display: "flex",
              justifyContent: "space-between",
              height: "2.25rem",
            }}
          >
            <p>Total</p>
            <p>$349.00</p>
          </div>
          <button
            style={{
              backgroundColor: "#fe324d",
              color: "#fff",
              border: "1rem #16181a",
              width: "12rem",
              height: "3rem",
              transition: "background-color 0.3s  ease",
              outline: "#fe324d solid 2px",
            }}
            className="boton"
            onMouseOver={(e) => {
              e.currentTarget.style.backgroundColor = "transparent";
              e.currentTarget.style.outline = "#fe324d solid 2px";
              e.currentTarget.style.color = "#fe324d ";
            }}
            onMouseOut={(e) => {
              e.currentTarget.style.backgroundColor = "#fe324d";
              e.currentTarget.style.outline = "#fe324d solid 2px";
              e.currentTarget.style.color = "#fff";
            }}
            // onClick={() => addToCart(id, price ,amount ,product)}
            // onFocus={(e) => e.currentTarget.style.outline = 'none'}
          >
            Update Totals
          </button>
        </div>
      </div>
    </div>
  );
};

export default FullCart;
