import "../App.css";
import Button from "react-bootstrap/Button";
import Card from "react-bootstrap/Card";
import { useState,useEffect } from "react";
import Productt from "./product";
import { DotPulse } from "@uiball/loaders";
import axios from "axios";



const Products1 = () => {



    const [products, setProducts] = useState(null);
    const [filteredProducts, setFilteredProducts] = useState(null);
    async function getData() {
      let products = await fetch("https://fakestoreapi.com/products");
      products = await products.json();
      setProducts(products);
    }
    useEffect(() => {
      getData();
        // axios.get('https://fakestoreapi.com/products').then((resp)=>setProducts(resp.data.products)).catch((err)=>console.log(err))
    }, []);

   const newArrival = () =>{
   
    const filteredData =products.filter((item) =>{
     
        return item.category === "men's clothing"
    })
    setFilteredProducts(filteredData);
   }
     
   
   const BestSellers = () =>{
    const filteredData =products.filter((item) =>{
        return item.category === "women's clothing"
    })
    setFilteredProducts(filteredData);
   }

   const Featured= () => {
    const filteredData = products.filter((item) => {
      return item.category === "electronics";
    });
    setFilteredProducts(filteredData);
  };
  const SpecialOffer = () => {
    const filteredData = products.filter((item) => {
      return item.category === "jewelery";
    });
    setFilteredProducts(filteredData);
  };

  return (
    <>
      <div className="container w-100 pro">
        <h1 style={{ textAlign: "center" }}>Exclusive Products</h1>
        <ul className="ulul" style={{ display: "flex", justifyContent: "space-evenly" }}>
          <li style={{cursor:'pointer'}}  onClick={newArrival}> New Arrival </li>
          <li style={{cursor:'pointer'}}  onClick={BestSellers}> Best Sellers </li>
          <li style={{cursor:'pointer'}}  onClick={Featured}> Featured </li>
          <li style={{cursor:'pointer'}}  onClick={SpecialOffer}> Special Offer</li>
        </ul>
        <div className="cards" style={{ display: "flex", flexWrap: "wrap", justifyContent:'space-evenly' }}>
        {products ? (
        <Productt products={filteredProducts ? filteredProducts : products} />
      ) : (
        <h1>Loading...</h1>
      )}
        </div>
      </div>
    </>
  );
};

export default Products1;
