import Breadcrumb from "react-bootstrap/Breadcrumb";
import styles from "../singlecss.module.css";
import { useState,useEffect,useRef } from "react";
import FacebookIcon from '@mui/icons-material/Facebook';
import GoogleIcon from '@mui/icons-material/Google';
import { useNavigate} from "react-router-dom";

import logcss from "./login.module.css";
import Form from "react-bootstrap/Form";
import { NavLink } from "react-router-dom";
import { Container } from "react-bootstrap";



const LOGIN = () => {


    const [rememberMe, setRememberMe] = useState(false);
    const [email, setEmail] =useState('')
    const [password, setPassword] =useState('')
    const navigate = useNavigate();
    const warning = useRef()

    useEffect(() =>{
        const user_id = localStorage.getItem("user_id")
        if(user_id){
          navigate("/")
        }
    },[])


    let users =[]
    if(JSON.parse(localStorage.getItem('users')) != null){
              users =JSON.parse(localStorage.getItem('users'))
                
            };

    function logg(e){
      e.preventDefault()
      let user =  users.filter((user) =>{
        return user.email === email &&  user.password=== password;
      })
       console.log(user)
       if (user.length ==  1){
        const loggedInUser_id = user.user_id;
        localStorage.setItem("user_id", loggedInUser_id)
        navigate('/home')
       }else{
        warning.current.style.display = "block"
       }
    }

    
   

    const handleSubmit = (e) =>{
      e.preventDefault();
      console.log(email)
      console.log(password)
    }

  const handleRememberMeChange = () => {
    setRememberMe(!rememberMe);
  };

  useEffect(() => {
    const storedRememberMe = localStorage.getItem('rememberMe');
    if (storedRememberMe) {
      setRememberMe(JSON.parse(storedRememberMe));
    }
  }, []);

  useEffect(() => {
    localStorage.setItem('rememberMe', JSON.stringify(rememberMe));
  }, [rememberMe]);

  return (
  <>
   
  <div className={styles.breadcrumbs}>
        <p>Login</p>
        <Breadcrumb id={styles.bread}>
          <Breadcrumb.Item active>Home</Breadcrumb.Item>
          <Breadcrumb.Item active>Pages</Breadcrumb.Item>
          <Breadcrumb.Item active>Login</Breadcrumb.Item>
        </Breadcrumb>
      </div>
    <Container className={logcss.log}>
      <Form style={{height:'34rem'}} onSubmit={handleSubmit} className={logcss.for}>
      <h1 style={{textAlign:'start'}} >Login</h1>
        <Form.Group className="mb-3" controlId="formGroupEmail">
         
          <Form.Control value={email}  onChange={(e)=>setEmail(e.target.value)} name="email" type="email" placeholder="Your Email" />
        </Form.Group>
        <Form.Group className="mb-3" controlId="formGroupPassword">
          
          <Form.Control value={password} onChange={(e)=>setPassword(e.target.value)}  name="password" type="password" placeholder="Password" />
        </Form.Group>
        <p ref={warning} className={logcss.warning}> wrong password or email</p>
        <div >
       <div style={{display:'flex'}}>
       <label className={logcss.lab}>
           <input type="checkbox" checked={rememberMe} onChange={handleRememberMeChange} />
            Remember Me
        </label>
    
       <label   style={{marginLeft:'3rem'}}>
        <NavLink className={logcss.lab} style={{textDecoration:'none' ,color:'gray'}} to='/login'>Forgot password? </NavLink>
       </label>
       </div>
       <br/>
       {/* <br/> */}
        <button
         type="submit"
            style={{
              backgroundColor: "#fe324d",
              color: "white",
              border: "none",
              width: "23rem",
              height: "3rem",
              transition: "background-color 0.3s ease",
              outline: "none",
              marginBottom:'0.5rem'
            }}
            className={logcss.boton}
            onMouseOver={(e) => {
              e.currentTarget.style.backgroundColor = "transparent";
              e.currentTarget.style.outline = "#fe324d solid 2px";
              e.currentTarget.style.color = "#fe324d ";
            }}
            onMouseOut={(e) => {
              e.currentTarget.style.backgroundColor = "#fe324d";
              e.currentTarget.style.outline = "none";
              e.currentTarget.style.color = "#ffff ";
            }}
              onClick={logg}
            // onClick={() => addToCart(id, price ,amount ,product)}
            // onFocus={(e) => e.currentTarget.style.outline = 'none'}
          >
           Log In
          </button>
        </div>
        {/* <br/> */}
        <span>OR</span>
        <hr/>
        <div style={{}}>
        {/* <br/> */}
        <button style={{backgroundColor:'#334c83',color:'#ffff',outline:'none' ,borderRadius:"6rem"}}><FacebookIcon style={{backgroundColor:'334c83', color:'#ffff'}}/>FACEBOOK</button>
        <button style={{backgroundColor:'#d33d2b',color:'#ffff',outline:'none' ,borderRadius:"6rem" , marginLeft:'1rem'}}><GoogleIcon style={{backgroundColor:'d33d2b', color:'#ffff'}}/>GOOGLE</button>
        <br/>
        <br/>
         <span>Don't Have an Account? <NavLink id={logcss.sign} style={{textDecoration:'none' ,color:'grey'}} to='/register'> Sign Up Now</NavLink></span>
       
       
        </div>
      </Form>
    </Container>
    </>
  );
};

export default LOGIN;
