import { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { Link } from "react-router-dom";
import styles from "../singlecss.module.css";
import Breadcrumb from "react-bootstrap/Breadcrumb";
import { Container } from "react-bootstrap";
import GradeIcon from "@mui/icons-material/Grade";
import StarHalfIcon from "@mui/icons-material/StarHalf";
import AddModeratorIcon from '@mui/icons-material/AddModerator';
import AutorenewIcon from '@mui/icons-material/Autorenew';
import MonetizationOnIcon from '@mui/icons-material/MonetizationOn';
import Tabss from "./tabssingle";
import Productt from "./product";
import ShoppingCartIcon from '@mui/icons-material/ShoppingCart';
import { useDispatch } from "react-redux";
import { cartslice } from "../redux/cartslice";




function ProductDetails() {

  const dispatch = useDispatch()








  const [count, setCount] = useState(0);
  const [cartItems, setCartItems] = useState([]);
  let subtotal = [];

  const handleAddToCart = (item) => {
    setCartItems([...cartItems, item]);
  };


  const handleIncrement = () => {
    setCount(count + 1);
  };

  const handleDecrement = () => {
    setCount(count - 1);
  };

  const [product, setProduct] = useState([]);
  let { id } = useParams();
  async function getData() {
    let product = await fetch(`https://fakestoreapi.com/products/${id}`);
    product = await product.json();
    setProduct(product);
  }
  useEffect(() => {
    getData();
  }, []);



  const [color1, setColor1] = useState('#87554b');
  const [color2, setColor2] = useState('#333333');
  const [color3, setColor3] = useState('#da323f');


  const inputStyle = {
    borderRadius: '50%',
    height: '50px',
    width: '50px',
    marginRight: '10px',
    border: 'none',
    outline: 'none',
  };

  return (
    <>
      <div className={styles.breadcrumbs}>
        <p>Product Detail</p>
        <Breadcrumb
          id={styles.bread}
          
        >
          <Breadcrumb.Item active>Home</Breadcrumb.Item>
          <Breadcrumb.Item active>Pages</Breadcrumb.Item>
          <Breadcrumb.Item active>Product Detail</Breadcrumb.Item>
        </Breadcrumb>
      </div>


      <Container className={styles.container}>
        <div className={styles.images}>
          <div className={styles.image}>
            <img className={styles.bigImage} src={product.image} />
            <div className={styles.miniPhotos}>
              <img src={product.image}  style={{width:'5rem',margin:'1rem' ,border:'solid grey 0.1rem', padding:'1rem'}}  />
              <img src={product.image}  style={{width:'5rem',margin:'1rem' ,border:'solid grey 0.1rem', padding:'1rem'}}     />
              <img src={product.image}  style={{width:'5rem',margin:'1rem' ,border:'solid grey 0.1rem', padding:'1rem'}}  />
              <img src={product.image}  style={{width:'5rem',margin:'1rem' ,border:'solid grey 0.1rem', padding:'1rem'}}  />
            </div>
          </div>
        </div>
        <div className={styles.details}>
           <h2 style={{width:'30rem'}}>{product.title}</h2>
              <div className={styles.pricee}>
              $ {product.price}
                      <span
                        style={{
                          textDecorationLine: "line-through",
                          color: "gray",
                          fontWeight: "lighter",
                        }}
                      >
                        $55.25
                      </span>
                      <span style={{ color: "#8b9e55", fontWeight: "lighter" }}>
                        35% Off
                      </span>
                      <div className={styles.rate}>
                      <GradeIcon
                      style={{ color: "#f6bc3e", fontSize: "medium" }}
                    />
                    <GradeIcon
                      style={{ color: "#f6bc3e", fontSize: "medium" }}
                    />
                    <GradeIcon
                      style={{ color: "#f6bc3e", fontSize: "medium" }}
                    />
                    <GradeIcon
                      style={{ color: "#f6bc3e", fontSize: "medium" }}
                    />
                    <StarHalfIcon
                      style={{ color: "#f6bc3e", fontSize: "medium" }}
                    />
                    <span style={{ color: "gray", fontSize: "small" }}>
                      (15)
                    </span>

                      </div>
              </div>
              <p style={{width:'30rem'}}>{product.description}</p>
              <span><AddModeratorIcon style={{color:'#ff5c64'}}/> 1 Year AL Jazeera Brand Warranty</span>
              <br/>
              <br/>
              <span><AutorenewIcon style={{color:'#ff5c64'}}/> 30 Day Return Policy</span>
              <br/>
              <br/>
              <span><MonetizationOnIcon style={{color:'#ff5c64'}}/> Cash on Delivery available</span>
              <br/>
              <br/>
              <div>
                    
                   <input style={{ ...inputStyle, backgroundColor:color1 , width:'1.5rem',height:'2rem',cursor:'pointer' }} />
                   <input style={{ ...inputStyle, backgroundColor: color2, width:'1.5rem',height:'2rem',cursor:'pointer' }} />
                   <input style={{ ...inputStyle, backgroundColor: color3, width:'1.5rem',height:'2rem',cursor:'pointer' }} />
              </div>
              <br/>
              <div style={{display:'flex'}}>
                <span style={{border:'0.01rem solid gray', margin:'0.25rem', padding:'0.25rem', cursor:'pointer'}} >XS</span>
                <span style={{border:'0.01rem solid gray', margin:'0.25rem', padding:'0.25rem', cursor:'pointer'}} >S</span>
                <span style={{border:'0.01rem solid gray', margin:'0.25rem', padding:'0.25rem', cursor:'pointer'}} >M</span>
                <span style={{border:'0.01rem solid gray', margin:'0.25rem', padding:'0.25rem', cursor:'pointer'}} >L</span>
                <span style={{border:'0.01rem solid gray', margin:'0.25rem', padding:'0.25rem', cursor:'pointer'}} >XL</span>
              </div>
              <hr/>
              <div className={styles.counter}>
                
              <div style={{display:'flex',justifyContent:'space-between',textAlign:'center'}}>
                  <button style={{borderRadius:'80rem',border:0}}  onClick={handleIncrement}>+</button>
                   <span style={{justifyContent:'center',border:'0.1rem solid ',padding:'0.5rem',marginLeft:'0.5rem',marginRight:'0.5rem'}}>{count}</span>
                   <button style={{borderRadius:'80rem',border:0}} onClick={handleDecrement}>-</button>
               </div>


              <button
            style={{
              backgroundColor: "#fe324d",
              color: "white",
              border: "none",
              width: "10rem",
              height: "3rem",
              transition: "background-color 0.3s ease",
              outline: "none",
            }}
            onClick={() => dispatch(cartslice.actions.addtocart(product))}
            className="boton"
            onMouseOver={(e) => {
              e.currentTarget.style.backgroundColor = "transparent";
              e.currentTarget.style.outline = "#fe324d solid 2px";
              e.currentTarget.style.color = "#fe324d ";
            }}
            onMouseOut={(e) => {
              e.currentTarget.style.backgroundColor = "#fe324d";
              e.currentTarget.style.outline = "none";
              e.currentTarget.style.color = "#ffff ";
            }}
            // onClick={() => addToCart(id, price ,amount ,product)}
            // onFocus={(e) => e.currentTarget.style.outline = 'none'}
          >
          <ShoppingCartIcon/>  ADD TO CART
          </button>
                
              </div>
              <hr/>
              <span  style={{display:'flex'}}>SKU:<p>BE45VGRT</p></span>
              <span>Category:{product.category}</span>
              <br/>
              <br/>
            <span style={{display:'flex'}}>Tags : <p style={{marginLeft:'0.25rem',color:'black',fontWeight:'500'}}> {product.category} </p></span>
             <div className="col-12 col-sm-12 col-md-6 text-md-right m-3">
                    <span>Share:</span>
                    <a  className="text-dark mx-2 ml-3"><i className="fab fa-instagram"></i></a>
                    <a  className="text-dark mx-2 ml-3"><i className="fab fa-twitter"></i></a>
                    <a  className="text-dark mx-2 ml-3"><i className="fab fa-dribbble"></i></a>
                    <a className=" text-dark mx-2 ml-3"><i className="fab fa-github"></i></a>
            </div>
           
        </div>
        
      </Container>
      <Tabss className={styles.Tabsss}/>
      <hr/>
      {/* <Productt/> */}




    </>
  );
}

export default ProductDetails;
