import { Link, NavLink } from "react-router-dom";
import Button from "react-bootstrap/Button";
import Card from "react-bootstrap/Card";
import GradeIcon from "@mui/icons-material/Grade";
import StarHalfIcon from "@mui/icons-material/StarHalf";
import { useState,useEffect } from "react";
import { Container } from "react-bootstrap";
import ShoppingBasketIcon from "@mui/icons-material/ShoppingBasket";
import Backdrop from '@mui/material/Backdrop';
import Box from '@mui/material/Box';
import { useDispatch } from "react-redux";
import { cartslice } from "../redux/cartslice";
import { addtocart,removefromcart } from '../redux/cartslice';
import axios from "axios";



function Productt(props) {


const dispatch = useDispatch()



  const products = props.products;
  const [count, setCount] = useState(0);

  const handleIncrement = () => {
    setCount(count + 1);
  };
  const [hover, setHover] = useState(false);

  const handleMouseEnter = () => {
    setHover(true);
  };

  const handleMouseLeave = () => {
    setHover(false);
  };


  return (
    <>
      {products.map((item) => {
        return (
          <Card
          key={item.id}
          onMouseOver={handleMouseEnter}
          onMouseOut={handleMouseLeave}
            id="carddd"
            className="carddd"
            style={{
              width: "15rem",
              margin: "1rem",
            }}
          >
           
              <Card.Img
             
                style={{
                  width: "6rem",
                  textAlign: "center",
                  alignItems: "center",
                  height: "8rem",
                  marginLeft: "4rem",
                  marginTop: "0.5rem",
                }}
                src={item.image}
              />
              <hr style={{ color: "gray" }} />
              <Card.Body>
              <NavLink className="productLink" to={`/product/${item.id}`}>   <Card.Title className="title">{item.title}</Card.Title> </NavLink>
                <Card.Text className="price">
                  $ {item.price}
                  <span
                    style={{
                      textDecorationLine: "line-through",
                      color: "gray",
                      fontWeight: "lighter",
                    }}
                  >
                    $55.25
                  </span>
                  <span style={{ color: "#8b9e55", fontWeight: "lighter" }}>
                    35% Off
                  </span>
                </Card.Text>
                {/* <Button variant="primary">Go somewhere</Button> */}
                <GradeIcon style={{ color: "#f6bc3e", fontSize: "medium" }} />
                <GradeIcon style={{ color: "#f6bc3e", fontSize: "medium" }} />
                <GradeIcon style={{ color: "#f6bc3e", fontSize: "medium" }} />
                <GradeIcon style={{ color: "#f6bc3e", fontSize: "medium" }} />
                <StarHalfIcon
                  style={{ color: "#f6bc3e", fontSize: "medium" }}
                />
                <span style={{ color: "gray", fontSize: "small" }}>(15)</span>
                <br />
                <div onClick={() => dispatch(cartslice.actions.addtocart(item))}>
                  <ShoppingBasketIcon />
                </div>
              </Card.Body>
{/*             
              <Backdrop
                open={hover}
              
                sx={{ zIndex: (theme) => theme.zIndex.drawer + 1,height:'20rem' ,width:'20rem',backgroundColor:'gray' }}
              >
                <Box sx={{ color: "#fff" }}></Box>
              </Backdrop> */}
           
          </Card>
        );
      })}
    </>
  );
}
export default Productt;
