import './App.css';
import {
  createBrowserRouter,
  createRoutesFromElements,
  Router,
  RouterProvider,
  Route,
  
} from "react-router-dom";
import RootLayout from "./pages/rootlayout";
import Home from "./pages/home";
import ProductDetails from "./components/single";
import Products1 from "./components/pro1";
import CART from "./pages/CartOne";

import LOGIN from "./register/login";
import REIGSTER from "./register/register";

import ContactUs from "./pages/contactUs";
import FullCart from "./pages/FullCart";
import Errorr from "./pages/404";
import TERMS from "./pages/Terms";



const approuter = createBrowserRouter(
  createRoutesFromElements
  (
    <>
    <Route element={<RootLayout />} path="/">
    
      <Route element={<Home />} path="/home" />
      <Route element={<CART />} path="/cart" />
      <Route element={<LOGIN />} path="/" />
      <Route element={<LOGIN />} path="/login" />
      <Route element={<REIGSTER />} path="/register" />
      <Route element={<ContactUs/>} path="/contact" />
      <Route element={<FullCart/>} path="/fullcart" />
      <Route element={<Errorr/>} path="*" />
      <Route element={<Products1 />} path="/products" />
      <Route element={<TERMS/>} path="/terms" />
      <Route
        
        element={<ProductDetails />}
        path="/product/:id"
      />
    </Route>
    </>
  )
);



function App() {

  return <RouterProvider router={approuter}> </RouterProvider>
}

export default App;
