import Carousel from "react-multi-carousel";
import "react-multi-carousel/lib/styles.css";
import Card from "react-bootstrap/Card";
import GradeIcon from "@mui/icons-material/Grade";
import StarHalfIcon from "@mui/icons-material/StarHalf";
import { Container, Row, Col } from "react-bootstrap";
import { NavLink } from "react-router-dom";

import '../App.css'
const MultiCarsoule = ({ products, title }) => {



  const responsive = {
    superLargeDesktop: {
      // the naming can be any, depends on you.
      breakpoint: { max: 4000, min: 3000 },
      items: 5,
    },
    desktop: {
      breakpoint: { max: 3000, min: 1024 },
      items: 4,
    },
    tablet: {
      breakpoint: { max: 1024, min: 464 },
      items: 2,
      
    },
    mobile: {
      breakpoint: { max: 464, min: 0 },
      items: 2,
    },
  };
  return (
    <>
      <div className="multiCar" style={{width:'80%',marginLeft:'10rem'}}>
        <h2 style={{textAlign:'center',marginBottom:'2rem'}} className="title">Featured Products</h2>
        <Carousel responsive={responsive}>
          {
          products.map((item) => {
            return (
             <Container  style={{margin:0}}>
            
              <Card  className="card" style={{ width: "80%",height:"27rem", marginBottom:'5rem' }}>
                <NavLink className="productLink" to={`/product/${item.id}`}>
                  <Card.Img
                    style={{ width: "100%", height: "12rem" }}
                    variant="top"
                    src={item.image}
                  />
                  <hr style={{ color: "gray" }} />
                  <Card.Body>
                    <Card.Title className="title">{item.title}</Card.Title>
                    <Card.Text className="price">
                      $ {item.price}
                      <span
                        style={{
                          textDecorationLine: "line-through",
                          color: "gray",
                          fontWeight: "lighter",
                        }}
                      >
                        $55.25
                      </span>
                      <span style={{ color: "#8b9e55", fontWeight: "lighter" }}>
                        35% Off
                      </span>
                    </Card.Text>
                    <GradeIcon
                      style={{ color: "#f6bc3e", fontSize: "medium" }}
                    />
                    <GradeIcon
                      style={{ color: "#f6bc3e", fontSize: "medium" }}
                    />
                    <GradeIcon
                      style={{ color: "#f6bc3e", fontSize: "medium" }}
                    />
                    <GradeIcon
                      style={{ color: "#f6bc3e", fontSize: "medium" }}
                    />
                    <StarHalfIcon
                      style={{ color: "#f6bc3e", fontSize: "medium" }}
                    />
                    <span style={{ color: "gray", fontSize: "small" }}>
                      (15)
                    </span>
                  </Card.Body>
                </NavLink>
              </Card>
           
             </Container>

           
            );
          })}
       
       
   
        </Carousel>
      </div>
    </>
  );
};

export default MultiCarsoule;