import ccarasol from "../assets/carasol1.png";
import ccarasol2 from "../assets/carasol2.png";
import ccarasol3 from "../assets/carasol3.png";
import styles from '../css.module.css'

import Carousel from 'react-bootstrap/Carousel';
import Container from "react-bootstrap/Container";

function Casaol() {
  return (
<div  className={styles.all}  style={{display:'flex', justifyContent:'center'}}>
    
   <Carousel className={styles.ca} style={{ display:'flex', justifyContent:'center' ,width:'100rem',height:'34.5rem', marginBottom:'2rem'}}>

      <Carousel.Item  className={styles.it1} >
      <div className={styles.carasollll}  style={{ alignItems: "center",marginLeft:'15rem',marginTop:'10rem' }}>
          <p>50% off in all products</p>
          <h1 style={{ width: "20rem" }}>Man Fashion</h1>
          <button
            style={{
              backgroundColor: "#fe324d",
              color: "white",
              border: "none",
              width: "10rem",
              height: "3rem",
              transition: "background-color 0.3s ease",
              outline: "none",
            }}
            className="boton"
            onMouseOver={(e) => {
              e.currentTarget.style.backgroundColor = "transparent";
              e.currentTarget.style.outline = "#fe324d solid 2px";
              e.currentTarget.style.color = "#fe324d ";
            }}
            onMouseOut={(e) => {
              e.currentTarget.style.backgroundColor = "#fe324d";
              e.currentTarget.style.outline = "none";
              e.currentTarget.style.color = "#ffff ";
            }}
            // onFocus={(e) => e.currentTarget.style.outline = 'none'}
          >
            SHOP NOW
          </button>
        </div> 
        {/* <img
        
          className="d-block w-100"
          src={ccarasol}
          alt="First slide"
        /> */}
       
      </Carousel.Item>
      <Carousel.Item className={styles.it2}>
      <div className={styles.carasollll}  style={{ alignItems: "center",marginLeft:'15rem',marginTop:'10rem' }}>
          <p>Taking your Viewing Experience to Next Level</p>
          <h1 style={{ width: "20rem" }}>Summer Sale</h1>
          <button
            style={{
              backgroundColor: "#fe324d",
              color: "white",
              border: "none",
              width: "10rem",
              height: "3rem",
              transition: "background-color 0.3s ease",
              outline: "none",
            }}
            className="boton"
            onMouseOver={(e) => {
              e.currentTarget.style.backgroundColor = "transparent";
              e.currentTarget.style.outline = "#fe324d solid 2px";
              e.currentTarget.style.color = "#fe324d ";
            }}
            onMouseOut={(e) => {
              e.currentTarget.style.backgroundColor = "#fe324d";
              e.currentTarget.style.outline = "none";
              e.currentTarget.style.color = "#ffff ";
            }}
            // onFocus={(e) => e.currentTarget.style.outline = 'none'}
          >
            SHOP NOW
          </button>
        </div>
        {/* <img
          className="d-block w-100"
          src={ccarasol2}
          alt="Second slide"
        /> */}

        
      </Carousel.Item>
       <Carousel.Item className={styles.it3}>
      <div className={styles.carasollll}  style={{ alignItems: "center",marginLeft:'15rem',marginTop:'10rem' }}>
      
          <h1 style={{ width: "20rem" }}>Woman Fashon</h1>
          <button
            style={{
              backgroundColor: "#fe324d",
              color: "white",
              border: "none",
              width: "10rem",
              height: "3rem",
              transition: "background-color 0.3s ease",
              outline: "none",
            }}
            className="boton"
            onMouseOver={(e) => {
              e.currentTarget.style.backgroundColor = "transparent";
              e.currentTarget.style.outline = "#fe324d solid 2px";
              e.currentTarget.style.color = "#fe324d ";
            }}
            onMouseOut={(e) => {
              e.currentTarget.style.backgroundColor = "#fe324d";
              e.currentTarget.style.outline = "none";
              e.currentTarget.style.color = "#ffff ";
            }}
            // onFocus={(e) => e.currentTarget.style.outline = 'none'}
          >
            SHOP NOW
          </button>
        </div>
        {/* <img
          className="d-block w-100"
          src={ccarasol3}
          alt="Third slide"
        /> */}

        
      </Carousel.Item> 
    </Carousel>
</div>
  );
}

export default Casaol;