import { Carousel } from "react-bootstrap";
import one from "../assets/one.png";
import two from "../assets/two.png";
import three from "../assets/three.png";
import four from "../assets/four.png";
import styles from "../css.module.css";

const BannerTWo = () => {
  return (
    <>
      <div
        className={styles.alll}
        style={{
          backgroundColor: "#fff1f1",
          width: "99vw",
          height: "25rem",
          marginBottom: "5rem",
          display: "flex",
        }}
      >
        <h3 style={{ position: "relative", top: 80, left: 650,width:'20rem' }}>
          Our Client Say!
        </h3>
        <Carousel className={styles.z}>
          <Carousel.Item>
            <img
              className="d-block w-75"
              src={one}
              alt="First slide"
            />
          
          </Carousel.Item>
          <Carousel.Item>
            <img
              className="d-block w-75"
              src={two}
              alt="Second slide"
            />

           
          </Carousel.Item>
          <Carousel.Item>
            <img
              className="d-block w-75"
              src={three}
              alt="Third slide"
            />

            
          </Carousel.Item>
          <Carousel.Item>
            <img
              className="d-block w-75"
              src={four}
              alt="Third slide"
            />

            
          </Carousel.Item>
        </Carousel>
      </div>
    </>
  );
};

export default BannerTWo;
