import ban1 from "../assets/tranding_img.png";
import styless from '../css.module.css'
const BannerOne = () => {
  return (
    <>
      <div
        className={styless.containerr}
        style={{
          backgroundColor: "#f4f9fc",
          display: "flex",
          justifyContent: "space-evenly",
          marginBottom: "10rem",
           height:'29rem',
        }}
      >
        <img src={ban1} style={{ height: "28rem" ,marginBottom:'-5rem '}} />
        <div className={styless.divv} style={{ position: "relative", top: "7rem" }}>
          <h6 style={{ color: "#fe324d" }}>New Season trends!</h6>
          <h1>Best Summer Collection</h1>
          <p>Sale Get up to 50% Off</p>
          <button
            style={{
              backgroundColor: "#fe324d",
              color: "white",
              border: "none",
              width: "10rem",
              height: "3rem",
              transition: "background-color 0.3s ease",
              outline: "none",
            }}
            className="boton"
            onMouseOver={(e) => {
              e.currentTarget.style.backgroundColor = "transparent";
              e.currentTarget.style.outline = "#fe324d solid 2px";
              e.currentTarget.style.color = "#fe324d ";
            }}
            onMouseOut={(e) => {
              e.currentTarget.style.backgroundColor = "#fe324d";
              e.currentTarget.style.outline = "none";
              e.currentTarget.style.color = "#ffff ";
            }}
            // onFocus={(e) => e.currentTarget.style.outline = 'none'}
          >
            SHOP NOW
          </button>
        </div>
      </div>
    </>
  );
};

export default BannerOne;
