import Tab from "react-bootstrap/Tab";
import Tabs from "react-bootstrap/Tabs";
import Table from 'react-bootstrap/Table';
import { Container } from "react-bootstrap";
import styles from '../singlecss.module.css'
const Tabss = () => {
  return (

    <Container >
    <Tabs
      defaultActiveKey="home"
      id="uncontrolled-tab-example"
      className={styles.tata}
      style={{width:'70rem',display:'flex'}}
    //   onClick={(title) =>{title.currentTarget.style.color='#fe324d'}}
    >
      <Tab className={styles.ti} eventKey="home" title="DESCRIPTION">
        <p style={{width:'69rem', margin:'1.5rem'}}>Contrary to popular belief, Lorem Ipsum is not simply random text. It has roots in a piece of classical Latin literature from 45 BC, making it over 2000 years old. Vivamus bibendum magna Lorem ipsum dolor sit amet, consectetur adipiscing elit.Contrary to popular belief, Lorem Ipsum is not simply random text. It has roots in a piece of classical Latin literature from 45 BC, making it over 2000 years old.</p>
        <p style={{width:'69rem', margin:'1.5rem'}}>At vero eos et accusamus et iusto odio dignissimos ducimus qui blanditiis praesentium voluptatum deleniti atque corrupti quos dolores et quas molestias excepturi sint occaecati cupiditate non provident, similique sunt in culpa qui officia deserunt mollitia animi, id est laborum et dolorum fuga. Et harum quidem rerum facilis est et expedita distinctio.</p>
      </Tab>
      <Tab eventKey="profile" title="ADDITIONAL INFO">
      <Table striped bordered hover>
      <thead className={styles.headd}>
        <tr>
          <th>Capacity</th>
          <th>Color</th>
          <th>Water Resistant</th>
          <th>Material</th>
        </tr>
      </thead>
      <tbody className={styles.boddy}>
        <tr>
          <td>5 Kg</td>
          <td>Black,Brown,Red</td>
          <td>YES</td>
          <td>Artificial Leather</td>
        </tr>
        </tbody>
    </Table>
      </Tab>
      <Tab eventKey="contact" title="REVIEWS(2)">
        Tab content for Contact
      </Tab>
    </Tabs>
    </Container>
  );
};

export default Tabss;
