import { NavLink } from "react-router-dom";
import styles from "../singlecss.module.css";
import Breadcrumb from "react-bootstrap/Breadcrumb";


const Errorr = () => {
    return (<>
   <div className={styles.breadcrumbs}>
        <p>Shopping Cart</p>
        <Breadcrumb id={styles.bread}>
          <Breadcrumb.Item active>Home</Breadcrumb.Item>
          <Breadcrumb.Item active>Pages</Breadcrumb.Item>
          <Breadcrumb.Item active>404</Breadcrumb.Item>
        </Breadcrumb>
      </div>
      <br />
      <div style={{textAlign:'center',marginBottom:'5rem'}}>
        <h1 style={{fontSize:'15rem'}}>404</h1>
        <h1>oops! The page you requested was not found!</h1>
        <p>The page you are looking for was moved, removed, renamed or might never existed.</p>
        <input style={{height:'50px',outline:'none',width:'25rem'}} type="email" placeholder="Enter Email Address" />
          <NavLink to='/home'>
      <button
            style={{
              backgroundColor: "#fe324d",
              color: "white",
              border: "none",
              width: "10rem",
              height: "3rem",
              transition: "background-color 0.3s ease",
              outline: "none",
            }}
             
            className="boton"
            onMouseOver={(e) => {
              e.currentTarget.style.backgroundColor = "transparent";
              e.currentTarget.style.outline = "#fe324d solid 2px";
              e.currentTarget.style.color = "#fe324d ";
            }}
            onMouseOut={(e) => {
              e.currentTarget.style.backgroundColor = "#fe324d";
              e.currentTarget.style.outline = "none";
              e.currentTarget.style.color = "#ffff ";
            }}
            // onClick={() => addToCart(id, price ,amount ,product)}
            // onFocus={(e) => e.currentTarget.style.outline = 'none'}
          >
          BACK TO HOME
          </button>
      </NavLink>     
      </div>
      
    </>  );
}
 
export default Errorr;
