import CardOne from "../components/cardOne";
import BannerOne from "../components/banner1";
import MiniCart from "../components/minicart";
import BannerTWo from "../components/banner2";
import "../App.css";
import { useEffect, useState } from "react";
import CartIcon from "../components/minicart";
import Products1 from "../components/pro1";
import Casaol from "../components/carasol";
import MostSaled from "../components/featuredProduct";
import DEVV from "../components/delevry";
import logcss from "../register/login.module.css";
import Dialog from "@mui/material/Dialog";
import DialogTitle from "@mui/material/DialogTitle";
import DialogContent from "@mui/material/DialogContent";
import DialogContentText from "@mui/material/DialogContentText";
import DialogActions from "@mui/material/DialogActions";
import Button from "@mui/material/Button";
import popup from '../assets/popup_img.jpg'
import CloseIcon from "@mui/icons-material/Close";
import CARTONNE from "./CartOne";
import Productt from "../components/product";

const Home = () => {
  



  const [showPopup, setShowPopup] = useState(false);

  useEffect(() => {
    setShowPopup(true);
  }, []);

  const handleClose = () => {
    setShowPopup(false);
  };

  return (
    <>
   
        <Dialog open={showPopup} onClose={handleClose}  >
        <DialogActions>
            <Button onClick={handleClose}>
              <CloseIcon />
            </Button>
          </DialogActions>
          <DialogContent style={{}}>
            <div style={{display:'flex',margin:'0rem',width:'33rem', gap:"2rem"}}>
         
              <img src={popup} style={{margin:'0rem',width:'10rem',height:"15rem"}}/>
              
              <div style={{display:'flex',flexDirection:'column'}}>
                <h3>Subscribe And Get 25% Discount!</h3>
                <p style={{width:'22rem'}}>
                
                  Subscribe to our newsletter and get the latest updates on new
                  products.
                </p>
                <input
                  style={{ height: "50px", outline: "none", width: "19rem",margin:'1rem' }}
                  type="email"
                  placeholder="Enter Email Address"
                />
                <button
                  type="submit"
                  style={{
                    backgroundColor: "#16181a",
                    color: "white",
                    margin:'1rem',
                    border: "none",
                    width: "17rem",
                    height: "3rem",
                    transition: "background-color 0.3s ease",
                    outline: "none",
                    marginBottom: "0.5rem",
                  }}
                  className={logcss.boton}
                  onMouseOver={(e) => {
                    e.currentTarget.style.backgroundColor = "transparent";
                    e.currentTarget.style.outline = "#16181a solid 2px";
                    e.currentTarget.style.color = "#16181a ";
                  }}
                  onMouseOut={(e) => {
                    e.currentTarget.style.backgroundColor = "#16181a";
                    e.currentTarget.style.outline = "none";
                    e.currentTarget.style.color = "#ffff ";
                  }}
                 
                  // onClick={() => addToCart(id, price ,amount ,product)}
                  // onFocus={(e) => e.currentTarget.style.outline = 'none'}
                >
                 Subscribe
                </button>
              </div>
            </div>
            
          </DialogContent>
         
        </Dialog>
   

      <Casaol />

      <CardOne />
      <Products1 />
   
      <br />
      <br />
      <br />
      <BannerOne />
      <MostSaled />
      <BannerTWo />
      <DEVV />
    </>
  );
};

export default Home;
